import React, { Component } from 'react';
import './App.css';
import { Map, Polygon, Polyline } from 'react-amap';

const plugins = ['Scale', 'OverView', 'ToolBar']
const numberReg = /\d+/;
class App extends Component {
  constructor() {
    super();

    this.state = {

      showInput: false,
      addMode: null,
      input: 'aaaa',
      polygonPath: [
        {longitude:120, latitude: 30},
        {longitude:120, latitude: 40},
        {longitude:130, latitude: 30},
        {longitude:130, latitude: 40}
      ],
      linePath: [],
    }

    this.addPolygon = this.addPolygon.bind(this);
    this.addLine = this.addLine.bind(this);
    this.onTxtChange = this.onTxtChange.bind(this);
  }

  showAddBox() {
    this.setState({
      showInput: true,
    });
  }

  addPolygon() {
    this.setState({
      addMode: 'polygon',
      input: '',
    })
  }

  addLine() {
    this.setState({
      addMode: 'line',
      input: '',
    })
  }

  onTxtChange(e) {
    this.setState({
      input: e.target.value
    }, () => {
      this.tryRender()
    })
  }

  tryRender() {
    let path = [];
    const list = this.state.input.split('\n');
    list.length && list.forEach((raw) => {
      const pos = raw.split(',');
      let lng = pos[0]
      let lat = pos[1]

      if (numberReg.test(lng) && numberReg.test(lat)) {
        path.push([Number(pos[0]), Number(pos[1])]);
      }
    })
    if (path.length) {
      if (this.state.addMode === 'line') {
        this.setState({
          linePath: path,
        })
      } else if (this.state.addMode === 'polygon') {
        if (path.length > 2) {
          path = path.map((lnglat) => {
            return {
              longitude: lnglat[0],
              latitude: lnglat[1],
            }
          })
          this.setState({
            polygonPath: path,
          })
        }
      }
    }
  }

  
  render() {
    return (
      <div className="App">
        <Map plugins={plugins}>
          <div className="mapTool">
            <div>
              <button className={this.state.addMode === 'polygon'? 'active': ''} type="primary" onClick={this.addPolygon}>Add Polygon</button>
              <button className={this.state.addMode === 'line'? 'active': ''} type="primary" onClick={this.addLine}>Add Line</button>
            </div>
            {
              this.state.addMode === null ? null :
              (
                <textarea 
                  value={this.state.input} 
                  name="" id="txt" cols="30" 
                  rows="10"
                  onChange={this.onTxtChange}
                ></textarea>
              )
            }
            
          </div>
          <div>
            
          </div>
          <Polygon path={this.state.polygonPath}/>
          <Polyline path={this.state.linePath}/>
        </Map>
      </div>
    );
  }
}

export default App;



